import React, { useState, useEffect } from 'react'
import MUIDataTable from 'mui-datatables'
import PageTitle from 'components/PageTitle'
import { PlaidLink } from 'react-plaid-link'
import { PLAID_PUBLIC_KEY } from 'helpers/utils'
import { Button, IconButton, ButtonGroup } from '@material-ui/core'
import EditIcon from '@material-ui/icons/Edit'
import DeleteIcon from '@material-ui/icons/Delete'
import { Chip } from '@material-ui/core'
import Backdrop from '@material-ui/core/Backdrop'
import CircularProgress from '@material-ui/core/CircularProgress'
import axios from 'axios'
import useStyles from "./styles"
import {
  ResponsiveContainer,
  ComposedChart,
  AreaChart,
  LineChart,
  Line,
  Area,
  PieChart,
  Pie,
  Cell,
  YAxis,
  XAxis,
} from "recharts"
import {
  Grid,
  LinearProgress,
  Select,
  OutlinedInput,
  MenuItem,
} from "@material-ui/core"
import Widget from "components/Widget"
import { Typography } from "components/Wrappers/Wrappers"
import Dot from "components/Sidebar/components/Dot"
import { useTheme } from "@material-ui/styles"
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles"

const options = {
  filter: true,
  filterType: 'dropdown',
  pagination: false,
}

const inOutColor = {
  'In': 'success',
  'Out': 'warning',
}

// const mainChartData = getMainChartData()

// #######################################################################
function getRandomData(length, min, max, multiplier = 10, maxDiff = 10) {
  var array = new Array(length).fill()
  let lastValue

  return array.map((item, index) => {
    let randomValue = Math.floor(Math.random() * multiplier + 1)

    while (
      randomValue <= min ||
      randomValue >= max ||
      (lastValue && randomValue - lastValue > maxDiff)
    ) {
      randomValue = Math.floor(Math.random() * multiplier + 1)
    }

    lastValue = randomValue

    return { value: randomValue }
  })
}

function getMainChartData() {
  var resultArray = []
  var Out = getRandomData(31, 3500, 6500, 7500, 1000)
  var In = getRandomData(31, 1500, 7500, 7500, 1500)

  for (let i = 0; i < Out.length; i++) {
    resultArray.push({
      Out: Out[i].value,
      In: In[i].value,
    })
  }

  return resultArray
}

const Dashboard = () => {
  var theme = useTheme()
  const classes = useStyles()
  const [userList, setUserList] = useState([])
  const [accountList, setAccountList] = useState([])
  const [transactionList, setTransactionList] = useState([])
  const [openLoader, setOpenLoader] = useState(false)
  const [mainChartState, setMainChartState] = useState("monthly")
  const [amountList, setAmountList] = useState([])
  const [mainChartData, setMainChartData] = useState([])
  const [maxAmount, setMaxAmount] = useState(0)
  const [InAmount, setInAmount] = useState([])
  const [OutAmount, setOutAmount] = useState([])

  const datatableColums = [
    {
      name: "Date",
    },
    {
      name: "Description",
    },
    {
      name: "Category",
    },
    {
      name: "Amount",
    },
    {
      name: "Currency Code",
    },
    {
      name: "In-Out",
      options: {
        filter: true,
        sort: true,
        empty: false,
        customBodyRender: (value, tableMeta, updateValue) => {
          const color = inOutColor[value]
          return (
            <Chip size='small' classes={{ root: classes[color] }} label={value} />
          )
        }
      }
    },
    {
      name: "Action",
      options: {
        filter: false,
        sort: false,
        empty: false,
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <ButtonGroup size='medium' variant="contained" aria-label="contained primary button group">
              <Button size='small' color='primary' style={{ padding: "0px" }}>edit</Button>
              <Button size='small' color='secondary' style={{ padding: "0px" }}>Del</Button>
            </ButtonGroup>
            // <div>
            //   <IconButton
            //     aria-label="delete"
            //     style={{ padding: "0px" }}
            //     size='medium'
            //     color="primary"
            //     variant="contained"
            //     onClick={handleEditDialogOpen}
            //   >
            //     <EditIcon size='small'/>
            //   </IconButton>
            //   <IconButton
            //     aria-label="delete"
            //     style={{ padding: "0px" }}
            //     size='medium'
            //     color="primary"
            //     variant="contained"
            //     onClick={handleEditDialogOpen}
            //   >
            //     <DeleteIcon size='small'/>
            //   </IconButton>
            // </div>
          )
        }
      }
    }
  ]

  const handleEditDialogOpen = () => { }
  const handleOnExit = () => { }
  const handleRefresh = async () => {
    await axios.get('/api/plaid/transactions').then(res => {
      if (res.data.transactions) {
        setAccountList(res.data.transactions.accounts)
        setTransactionList(res.data.transactions.transactions)
        let resIn = []
        let resOut = []
        let maxIn = 0
        let maxOut = 0
        let dateList = []
        res.data.transactions.transactions.map(item => {
          if (item.amount >= 0) {
            !resIn[item.date] && (resIn[item.date] = 0)
            resIn[item.date] += item.amount
            maxIn = item.amount > maxIn ? item.amount : maxIn
          } else {
            !resOut[item.date] && (resOut[item.date] = 0)
            resOut[item.date] -= item.amount
            maxOut = item.amount < maxOut ? item.amount : maxOut
          }
          !dateList.includes(item.date) && dateList.push(item.date)
        })
        setMaxAmount(Math.max(Math.abs(maxOut), maxIn))
        setInAmount(resIn)
        setOutAmount(resOut)
        let chartData = dateList.map(date => {
          return {
            In: resIn[date] | null,
            Out: resOut[date] | null
          }
        })
        setMainChartData(chartData)
      }
    })
  }
  const handleOnSuccess = async (public_token, metadata) => {
    setOpenLoader(true)
    await axios.post('/api/plaid/auth/public_token', {
      public_token: public_token
    })
    await handleRefresh()
    setOpenLoader(false)
  }

  return (
    <div>
      <Backdrop className={classes.backdrop} open={openLoader}>
        <CircularProgress color="inherit" />
      </Backdrop>

      <PageTitle title='Transaction Overview' button='Refresh Transaction' onClick={handleRefresh} />

      <div>
        <PlaidLink
          clientName='React Plaid Setup'
          env='sandbox'
          product={['auth', 'transactions']}
          publicKey={PLAID_PUBLIC_KEY}
          onExit={handleOnExit}
          onSuccess={handleOnSuccess}
          className='choose-bank-btn'
        >
          <Button
            variant='contained'
            size='large'
            color='primary'>Open Link and connect your bank!</Button>
        </PlaidLink>
      </div>

      <Widget
        bodyClass={classes.mainChartBody}
        header={
          <div className={classes.mainChartHeader}>
            <Typography
              variant="h5"
              color="text"
              colorBrightness="secondary"
            >
              Transactions In-Out Line Chart
                </Typography>
            <div className={classes.mainChartHeaderLabels}>
              <div className={classes.mainChartHeaderLabel}>
                <Dot color="warning" />
                <Typography className={classes.mainChartLegentElement}>
                  Out
                    </Typography>
              </div>
              <div className={classes.mainChartHeaderLabel}>
                <Dot color="success" />
                <Typography className={classes.mainChartLegentElement}>
                  In
                    </Typography>
              </div>
            </div>
            <Select
              value={mainChartState}
              onChange={e => setMainChartState(e.target.value)}
              input={
                <OutlinedInput
                  labelWidth={0}
                  classes={{
                    notchedOutline: classes.mainChartSelectRoot,
                    input: classes.mainChartSelect,
                  }}
                />
              }
              autoWidth
            >
              <MenuItem value="daily">Daily</MenuItem>
              <MenuItem value="weekly">Weekly</MenuItem>
              <MenuItem value="monthly">Monthly</MenuItem>
            </Select>
          </div>
        }
      >
        <ResponsiveContainer width="100%" minWidth={500} height={350}>
          <ComposedChart
            margin={{ top: 0, right: -15, left: -15, bottom: 0 }}
            data={mainChartData}
          >
            <YAxis
              ticks={[0, maxAmount / 3, maxAmount / 3 * 2, maxAmount]}
              tick={{ fill: theme.palette.text.hint + "80", fontSize: 14 }}
              stroke={theme.palette.text.hint + "80"}
              tickLine={false}
            />
            <XAxis
              tickFormatter={i => i + 1}
              tick={{ fill: theme.palette.text.hint + "80", fontSize: 14 }}
              stroke={theme.palette.text.hint + "80"}
              tickLine={false}
            />
            <Line
              type="linear"
              dataKey="In"
              stroke={theme.palette.success.main}
              strokeWidth={2}
              dot={false}
              activeDot={false}
            />
            <Line
              type="linear"
              dataKey="Out"
              stroke={theme.palette.warning.main}
              strokeWidth={2}
              dot={{
                stroke: theme.palette.warning.dark,
                strokeWidth: 2,
                fill: theme.palette.warning.main,
              }}
            />
          </ComposedChart>
        </ResponsiveContainer>
      </Widget>
      <br></br>
      <MuiThemeProvider theme={myTheme}>
        <MUIDataTable
          size='small'
          title='Transactions History'
          data={transactionList.map((item, index) => {
            const inOut = item.amount >= 0 ? 'In' : 'Out'

            return [
              item.date,
              item.name,
              item.category[0],
              item.amount,
              item.iso_currency_code,
              inOut,
            ]
          })}
          columns={datatableColums}
          options={options}
        />
      </MuiThemeProvider>

    </div>
  )
}

export default Dashboard

const myTheme = createMuiTheme({
  overrides: {
    MUIDataTableBodyCell: {
      root: {
        height: "20px",
      }
    }
  }
})

