import React, { useState, useEffect, useCallback } from 'react'
import { usePlaidLink } from 'react-plaid-link'
import Backdrop from '@material-ui/core/Backdrop'
import CircularProgress from '@material-ui/core/CircularProgress'
import { Chip } from '@material-ui/core'
import { Button, IconButton, ButtonGroup } from '@material-ui/core'
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles"
import MUIDataTable from 'mui-datatables'
import useStyles from "./styles"
import { PLAID_PUBLIC_KEY } from 'helpers/utils'
import axios from 'axios'

const options = {
  filter: true,
  filterType: 'dropdown',
  pagination: false,
  responsive: 'scroll',
}

const inOutColor = {
  'In': 'success',
  'Out': 'warning',
}

const PlaidBank = () => {
  const [flag, setFlag] = useState(true)
  const [openLoader, setOpenLoader] = useState(false)
  const [transactionList, setTransactionList] = useState([])
  const [accountList, setAccountList] = useState([])
  const classes = useStyles()
  let balance = 0

  useEffect(() => {
    flag && open()
  })

  const onSuccess = useCallback(async (public_token, metadata) => {
    setFlag(false)
    setOpenLoader(true)
    await axios.post('/api/plaid/auth/public_token', {
      public_token: public_token
    })
    await handleRefresh()
    setOpenLoader(false)
  }, [])

  const config = {
    clientName: 'Verifunds.ca',
    env: 'sandbox',
    product: ['auth', 'transactions'],
    publicKey: PLAID_PUBLIC_KEY,
    language: 'en',
    countryCodes: ['Ca'],
    onSuccess
  }

  const { open, ready, error } = usePlaidLink(config)

  const handleRefresh = async () => {
    await axios.get('/api/plaid/transactions').then(res => {
      if (res.data.transactions) {
        setAccountList(res.data.transactions.accounts)
        setTransactionList(res.data.transactions.transactions)
      }
    })
  }

  const datatableColums = [
    {
      name: "Date",
    },
    {
      name: "Description",
    },
    {
      name: "Category",
    },
    {
      name: "In",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          const color = inOutColor['In']
          return (
            <>
              { value !== '' && <Chip size='small' classes={{ root: classes[color] }} label={value} />}
            </>
          )
        }
      }
    },
    {
      name: "Out",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          const color = inOutColor['Out']
          return (
            <>
              { value !== '' && <Chip size='small' classes={{ root: classes[color] }} label={value} />}
            </>
          )
        }
      }
    },
    {
      name: "Balance",
    },
    {
      name: "Currency Code",
    },
    {
      name: "Action",
      options: {
        filter: false,
        sort: false,
        empty: false,
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <ButtonGroup size='medium' variant="contained" aria-label="contained primary button group">
              <Button size='small' color='primary' style={{ padding: "0px" }}>edit</Button>
              <Button size='small' color='secondary' style={{ padding: "0px" }}>Del</Button>
            </ButtonGroup>
          )
        }
      }
    }
  ]

  return (
    <div style={{ padding: '20px' }}>
      <Backdrop className={classes.backdrop} open={openLoader}>
        <CircularProgress color="inherit" />
      </Backdrop>

      { !flag &&
        <>
          <h1>Transaction List</h1>
          <MuiThemeProvider theme={myTheme}>
            <MUIDataTable
              size='small'
              title='Transactions History'
              data={transactionList.map((item, index) => {
                const inOut = item.amount >= 0 ? 'In' : 'Out'
                balance += item.amount
                balance = Math.floor(balance * 100) / 100
                return [
                  item.date,
                  item.name,
                  item.category[0],
                  inOut === 'In' ? item.amount : '',
                  inOut === 'Out' ? item.amount : '',
                  balance,
                  item.iso_currency_code,
                ]
              })}
              columns={datatableColums}
              options={options}
            />
          </MuiThemeProvider>
        </>
      }
    </div >
  )
}

export default PlaidBank

const myTheme = createMuiTheme({
  overrides: {
    MUIDataTable: {
      responsiveScroll: {
        maxHeight: "700px"
      }
    },
    MUIDataTableBodyCell: {
      root: {
        height: "20px",
      }
    }
  }
})
