import React, { useState, useEffect } from 'react'
import MUIDataTable from "mui-datatables"
import PageTitle from "components/PageTitle"
import { PlaidLink } from 'react-plaid-link'
import { PLAID_PUBLIC_KEY } from 'helpers/utils'
import { Button } from "@material-ui/core"

import './Bank.scss'

const options = {
  filter: true,
  filterType: "dropdown",
}

const Bank = () => {
  const [userList, setUserList] = useState([])

  const datatableColums = [
    {
      name: "Date",
    },
    {
      name: "Bank Name",
    },
    {
      name: "Description",
    },
    {
      name: "Action",
      options: {
        filter: false,
        sort: false,
        empty: false,
        // customBodyRender: (value, tableMeta, updateValue) => {
        //   return (
        //     <Button color="primary" variant="contained" onClick={handleEditDialogOpen}>
        //       Edit
        //     </Button>
        //   )
        // }
      }
    }
  ]
  
  const handleOnExit = () => { }
  const handleOnSuccess = () => { }

  return (
    <div>
      <PageTitle title='Bank History' />

      <div>
        <PlaidLink
          clientName="React Plaid Setup"
          env="sandbox"
          product={["auth", "transactions"]}
          publicKey={PLAID_PUBLIC_KEY}
          onExit={handleOnExit}
          onSuccess={handleOnSuccess}
          className="choose-bank-btn"
        >
          <Button
            variant="contained"
            size="large"
            color="secondary">Please Choose your Bank</Button>
        </PlaidLink>
      </div>

      <MUIDataTable
        title="Bank History"
        data={userList}
        columns={datatableColums}
        options={options}
      />

    </div>
  )
}


export default Bank
